<template>
  <div>
    <el-breadcrumb separator="/" style="margin-bottom: 50px;">
      <el-breadcrumb-item to="/">工作台</el-breadcrumb-item>
      <el-breadcrumb-item to="/itemdq/rule">短期项目</el-breadcrumb-item>
      <el-breadcrumb-item>设置</el-breadcrumb-item>
    </el-breadcrumb>
    <el-form :inline="true" @submit.native.prevent="onSubmit" :model="form" class="form-search">
      <el-row>
        <el-col>
          <el-form-item label="项目名">
            <el-input size="small" placeholder="请输入内容" v-model="form.name"> </el-input>
            <el-input size="small" placeholder="请输入内容（英文）" v-model="form.name_en"> </el-input>
            <el-input size="small" placeholder="请输入内容（越南）" v-model="form.name_yn"> </el-input>
          </el-form-item>
        </el-col>
        <el-col>
          <el-form-item label="项目图片">
            <el-upload
              :limit="1"
              :action="'/api/api/upload?ssid=' + ssid()"
              :on-success="fileSuccess"
              :file-list="file"
              list-type="picture"
            >
              <el-button size="small" type="infor">点击上传</el-button>
              <div slot="tip" style="color: red" class="el-upload__tip">
                尺寸:宽750px 高:350px 格式:png、jpg、jpeg、gif
              </div>
            </el-upload>
          </el-form-item>
        </el-col>

        <el-col>
          <el-form-item label="是否显示在首页">
            <el-select size="small" v-model="form.is_show_index" clearable placeholder="请选择">
              <el-option :value="key" :label="value" :key="value" v-for="(value, key) in config.is_show_index" />
            </el-select>
          </el-form-item>
        </el-col>

        <el-col>
          <el-form-item label="类型">
            <el-select size="small" v-model="form.type" clearable placeholder="请选择">
              <el-option :value="key" :label="value" v-for="(value, key) in config.type" :key="value" />
            </el-select>
          </el-form-item>
        </el-col>

        <el-col>
          <el-form-item label="状态">
            <el-select size="small" v-model="form.status" clearable placeholder="请选择">
              <el-option v-for="(value, key) in config.status" :value="key" :label="value" :key="value" />
            </el-select>
          </el-form-item>
        </el-col>

        <el-col>
          <el-form-item label="排名">
            <el-input size="small" placeholder="请输入内容" v-model="form.sort">
              <template slot="append">数字越大，排名越靠前</template>
            </el-input>
          </el-form-item>
        </el-col>

        <!-- <el-col>
          <el-form-item label="当前进度">
            <el-input size="small" placeholder="请输入内容" v-model="form.schedule">
              <template slot="append">%为100时自动关闭</template>
            </el-input>
          </el-form-item>
        </el-col> -->

        <el-col>
          <el-form-item label="投资时长">
            <el-input size="small" type="number" placeholder="请输入内容" v-model="form.days">
              <template slot="append">天</template>
            </el-input>
          </el-form-item>
        </el-col>

        <el-col>
          <el-form-item label="开始时间">
            <el-date-picker v-model="date" type="datetime" placeholder="选择日期时间" default-time="12:00:00">
            </el-date-picker>
          </el-form-item>
        </el-col>

        <el-col>
          <el-form-item label="组件总数">
            <el-input size="small" type="number" step="1" placeholder="请输入内容" v-model="form.stock">
              <template slot="append">件</template>
            </el-input>
          </el-form-item>
        </el-col>
        <el-col>
          <el-form-item label="剩余组件">
            <el-input size="small" type="number" step="1" placeholder="请输入内容" v-model="form.rem_count">
              <template slot="append">件</template>
            </el-input>
          </el-form-item>
        </el-col>

        <el-col>
          <el-form-item label="利率">
            <el-input size="small" type="number" step="0.01" placeholder="请输入内容" v-model="form.apr">
              <template slot="append">%</template>
            </el-input>
          </el-form-item>
        </el-col>
        <el-col>
          <el-form-item label="红包额度">
            <el-input size="small" placeholder="请输入内容" v-model="form.pack">
              <template slot="append">元</template>
            </el-input>
          </el-form-item>
        </el-col>
        <el-col>
          <el-form-item label="满N元得一红包">
            <el-input size="small" type="number" step="0.01" placeholder="请输入内容" v-model="form.pack_money">
              <template slot="append">元</template>
            </el-input>
          </el-form-item>
        </el-col>
        <el-col>
          <el-form-item label="最大可获得红包数">
            <el-input size="small" type="number" step="0" placeholder="请输入内容" v-model="form.pack_max_num">
              <template slot="append">个</template>
            </el-input>
          </el-form-item>
        </el-col>
        <el-col>
          <el-form-item label="上级分润">
            <el-input size="small" placeholder="请输入内容" v-model="form.top_apr">
              <template slot="append">%</template>
            </el-input>
          </el-form-item>
        </el-col>
        <el-col>
          <el-form-item label="投资送抽奖次数">
            <el-input size="small" placeholder="请输入内容" v-model="form.prize_num">
              <template slot="append">次</template>
            </el-input>
          </el-form-item>
        </el-col>

        <el-col>
          <el-form-item label="最大投资金额">
            <el-input size="small" placeholder="请输入内容" v-model="form.max_money">
              <template slot="append">元</template>
            </el-input>
          </el-form-item>
        </el-col>

        <el-col>
          <el-form-item label="最小投资金额">
            <el-input size="small" placeholder="请输入内容" v-model="form.min_money">
              <template slot="append">元</template>
            </el-input>
          </el-form-item>
        </el-col>

        <el-col>
          <el-form-item label="最大投资次数">
            <el-input size="small" placeholder="请输入内容" v-model="form.max_count">
              <template slot="append">次</template>
            </el-input>
          </el-form-item>
        </el-col>
        <el-col>
          <el-form-item label="是否使用代金券">
            <el-switch
              v-model="is_open_v"
              active-color="#13ce66"
              inactive-color="#ff4949"
              active-text="开启代金券"
              inactive-text="关闭代金券"
              @change="vouchersHandle"
            >
            </el-switch>
          </el-form-item>
        </el-col>

        <el-col>
          <el-form-item label="代金券金额">
            <el-input
              size="small"
              placeholder="请输入内容"
              :disabled="form.if_open_vouchers == 'Y' ? false : true"
              v-model="form.vouchers_money"
            >
              <template slot="append">元</template>
            </el-input>
          </el-form-item>
        </el-col>

        <el-col>
          <el-form-item label="项目说明">
            <el-input
              size="small"
              type="textarea"
              :rows="12"
              :cols="20"
              placeholder="请输入内容"
              v-model="form.item_desc"
            >
              <template slot="append"></template>
            </el-input>
            <el-input
              size="small"
              type="textarea"
              :rows="12"
              :cols="20"
              placeholder="请输入内容（英文）"
              v-model="form.item_desc_en"
            >
              <template slot="append"></template>
            </el-input>
            <el-input
              size="small"
              type="textarea"
              :rows="12"
              :cols="20"
              placeholder="请输入内容（越南）"
              v-model="form.item_desc_yn"
            >
              <template slot="append"></template>
            </el-input>
          </el-form-item>
        </el-col>

        <el-col>
          <el-button native-type="submit" type="primary">提交</el-button>
        </el-col>
      </el-row>
    </el-form>
  </div>
</template>
<script>
import { Message } from 'element-ui'
import Fetch from '../../utils/fetch'

export default {
  data() {
    return {
      form: {
        prize_num: 0,
        name: '',
        min_money: '',
        max_money: '',
        schedule: '',
        is_show_index: 'N',
        apr: '',
        days: '',
        type: [],
        max_count: '',
        thumb: '',
        money: 0,
        begin_time: 0,
        if_open_vouchers: 'N',
        vouchers_money: 0,
        stock: 0,
        //多语言设置
        name_en: '',
        name_yn: '',
      },
      is_open_v: false,
      config: {
        status: {},
        sound_list: {},
        type: {},
        is_low_risk: null,
      },
      file: [],
      date: new Date(),
    }
  },
  mounted() {
    const id = this.$router.history.current.params.id
    var url = '/item/dq/config'
    if (id) {
      url = '/item/dq/view'
    }

    Fetch(url, { id: id }).then(r => {
      if (r.data.view) {
        if (r.data.view.if_open_vouchers == 'Y') {
          this.is_open_v = true
        } else {
          this.is_open_v = false
        }
        this.form = r.data.view
        this.config = r.data.config
        if (r.data.view.thumb) {
          this.file = [
            {
              url: r.data.view.thumb,
              name: r.data.view.thumb,
            },
          ]
        }

        if (r.data.view.begin_time > 0) {
          this.date = new Date(parseInt(r.data.view.begin_time) * 1000)
        }
      } else {
        this.config = r.data
      }
    })
  },
  methods: {
    ssid() {
      return localStorage.getItem('ssid')
    },
    vouchersHandle(res) {
      if (res) {
        this.form.if_open_vouchers = 'Y'
        this.is_open_v = true
        this.form.if_open_vouchers_name = '是'
      } else {
        this.form.if_open_vouchers = 'N'
        this.is_open_v = false
        this.form.if_open_vouchers_name = '否'
      }
    },
    onSubmit() {
      const id = this.$router.history.current.params.id
      var url = '/item/dq/create'
      if (id) {
        url = '/item/dq/update'
      }
      this.form.begin_time = Date.parse(this.date) / 1000
      Fetch(url, {
        id: this.$router.history.current.params.id,
        ...this.form,
      }).then(() => {
        Message.success('操作成功')
        this.$router.replace('/itemdq/rule')
      })
    },
    fileSuccess(res, file) {
      this.file = [file]
      this.form.thumb = res.data.file
    },
  },
}
</script>
<style lang="less" scoped>
.form-search {
  max-width: 800px;

  /deep/ .el-input {
    width: 300px;
  }

  /deep/ .el-form-item__label {
    width: 140px;
    text-align: right;
  }
}
</style>
